.input-wrapper {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 8px;
}

.cta-block {
  grid-column: span 2;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 8px;
}

.cta-block__back {
  grid-column: 1;
}

.cta-block__next {
  grid-column: 2;
}

.errorMessage {
  color: red;
  grid-column: span 2;
  margin: 0;
}
